import React from 'react';
import { HeadFC, PageProps, graphql } from 'gatsby';
import Layout from '../components/Layout/Layout';
import LabCard from '../components/LabCard/LabCard';

interface LabsPageProps extends PageProps {
  data: {
    allMdx: {
      nodes: {
        id: string;
        parent: {
          relativeDirectory: string;
        };
      }[];
    };
  };
}

const LabsPage: React.FC<LabsPageProps> = ({ data }) => {
  // Client-side grouping
  const nodes = data.allMdx.nodes;
  const groups: Record<string, number> = {};

  nodes.forEach((node) => {
    const dir = node.parent.relativeDirectory;
    if (dir) {
      groups[dir] = (groups[dir] || 0) + 1;
    }
  });

  const categories = Object.keys(groups).map((key) => ({
    fieldValue: key,
    totalCount: groups[key],
  }));

  const getCategoryInfo = (category: string) => {
    switch (category) {
      case `etymology`:
        return {
          title: `Etymology Explorer`,
          icon: `üìö`,
          description: `Greek & Latin roots.`,
        };
      case `phonetics`:
        return {
          title: `Phonetics Workshop`,
          icon: `üîä`,
          description: `Mastering sounds.`,
        };
      case `patterns`:
        return {
          title: `Spelling Patterns`,
          icon: `‚úçÔ∏è`,
          description: `Rules and exceptions.`,
        };
      case `logic`:
        return {
          title: `Logic Puzzles`,
          icon: `üß©`,
          description: `Linguistic nuances.`,
        };
      default:
        return {
          title: category.charAt(0).toUpperCase() + category.slice(1),
          icon: `üìÇ`,
          description: `Miscellaneous`,
        };
    }
  };

  return (
    <Layout>
      <div style={{ textAlign: `center`, marginBottom: `4rem` }}>
        <h1 style={{ fontSize: `3rem`, marginBottom: `1rem` }}>Word Labs</h1>
        <p
          style={{
            fontSize: `1.25rem`,
            color: `var(--color-text-secondary)`,
            maxWidth: `600px`,
            margin: `0 auto`,
          }}
        >
          Select a category to begin your training.
        </p>
      </div>

      <div
        style={{
          display: `grid`,
          gap: `2rem`,
          gridTemplateColumns: `repeat(auto-fit, minmax(340px, 1fr))`,
        }}
      >
        {categories.map((group) => {
          const info = getCategoryInfo(group.fieldValue);
          return (
            <LabCard
              key={group.fieldValue}
              title={info.title}
              description={info.description}
              slug={group.fieldValue} // Links to /labs/{category}
              level="Course"
              duration={`${group.totalCount} Labs`}
              topics={group.totalCount} // repurpose field for count
              icon={info.icon}
            />
          );
        })}
      </div>
    </Layout>
  );
};

export default LabsPage;

export const Head: HeadFC = () => (
  <title>Word Labs | Spelling Bee Toolbox</title>
);

export const query = graphql`
  query LabsCategoriesQuery {
    allMdx {
      nodes {
        id
        parent {
          ... on File {
            relativeDirectory
          }
        }
      }
    }
  }
`;
